	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import news from '@/views/news/list'
	import jingdianxinxi from '@/views/jingdianxinxi/list'
	import discussjiudianxinxi from '@/views/discussjiudianxinxi/list'
	import jiudianxinxi from '@/views/jiudianxinxi/list'
	import storeup from '@/views/storeup/list'
	import users from '@/views/users/list'
	import jiudianyudingxinxi from '@/views/jiudianyudingxinxi/list'
	import xingchengguihua from '@/views/xingchengguihua/list'
	import forum from '@/views/forum/list'
	import lvke from '@/views/lvke/list'
	import discussjingdianxinxi from '@/views/discussjingdianxinxi/list'
	import lvyouxianlu from '@/views/lvyouxianlu/list'
	import difangmeishi from '@/views/difangmeishi/list'
	import config from '@/views/config/list'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '首页',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '首页',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '修改密码',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/news',
			name: '美食资讯',
			component: news
		}
		,{
			path: '/jingdianxinxi',
			name: '景点信息',
			component: jingdianxinxi
		}
		,{
			path: '/discussjiudianxinxi',
			name: '酒店信息评论',
			component: discussjiudianxinxi
		}
		,{
			path: '/jiudianxinxi',
			name: '酒店信息',
			component: jiudianxinxi
		}
		,{
			path: '/storeup',
			name: '我的收藏',
			component: storeup
		}
		,{
			path: '/users',
			name: '管理员',
			component: users
		}
		,{
			path: '/jiudianyudingxinxi',
			name: '酒店预订信息',
			component: jiudianyudingxinxi
		}
		,{
			path: '/xingchengguihua',
			name: '行程规划',
			component: xingchengguihua
		}
		,{
			path: '/forum',
			name: '论坛交流',
			component: forum
		}
		,{
			path: '/lvke',
			name: '旅客',
			component: lvke
		}
		,{
			path: '/discussjingdianxinxi',
			name: '景点信息评论',
			component: discussjingdianxinxi
		}
		,{
			path: '/lvyouxianlu',
			name: '旅游线路',
			component: lvyouxianlu
		}
		,{
			path: '/difangmeishi',
			name: '地方美食',
			component: difangmeishi
		}
		,{
			path: '/config',
			name: '轮播图',
			component: config
		}
		]
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
